import {Component, ViewChild, ElementRef, OnInit, AfterViewInit} from '@angular/core';
import * as THREE from 'three';

@Component({
  selector: 'app-ortho',
  templateUrl: './ortho.component.html',
  styleUrls: ['./ortho.component.css']
})
export class OrthoComponent implements OnInit, AfterViewInit {

  // @ts-ignore
  @ViewChild('rendererContainer') rendererContainer: ElementRef;

  name = 'Angular';
  renderer: THREE.WebGLRenderer;
  camera: THREE.OrthographicCamera;
  // camera: THREE.PerspectiveCamera;
  scene: THREE.Scene;

  constructor() {
    this.scene = new THREE.Scene();
    this.camera = new THREE.OrthographicCamera(0, 500, 0, 500, 0.1, 100 );
    // this.camera = new THREE.PerspectiveCamera( 75, window.innerWidth / window.innerHeight, 0.1, 1000 );

    this.renderer = new THREE.WebGLRenderer();
    this.renderer.setSize( window.innerWidth, window.innerHeight );

    this.camera.position.z = 5;
  }

  // @ts-ignore
  onResize = event => {
    console.log(event.target.innerWidth, event.target.innerHeight);
    // this.camera.aspect = (event.target.innerWidth)  / (event.target.innerHeight);
    this.renderer.setSize( (event.target.innerWidth), (event.target.innerHeight) );
    this.camera.updateProjectionMatrix();
  }

  ngOnInit(): void {
  }

  ngAfterViewInit(): void {
    this.rendererContainer.nativeElement.appendChild( this.renderer.domElement );
    const geometry = new THREE.BoxGeometry( 1, 1, 1 );
    const texture = new THREE.TextureLoader().load('assets/images/lineup/gress.png');
    // const material = new THREE.MeshBasicMaterial( { color: 0x00ff00 } );
    const material = new THREE.MeshBasicMaterial( { map: texture } );
    const cube = new THREE.Mesh( geometry, material );
    this.scene.add( cube );

    const geometry2 = new THREE.PlaneGeometry( 100, 100 );
    const material2 = new THREE.MeshBasicMaterial( { map: texture, side: THREE.DoubleSide} );
    const plane = new THREE.Mesh( geometry, material );
    this.scene.add( plane );

    const animate =  () => {
      requestAnimationFrame( animate );

      cube.rotation.x += 0.01;
      cube.rotation.y += 0.01;

      this.renderer.render( this.scene, this.camera );
    };

    animate();
  }
}
